@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Montserrat" "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.color-gray {
  color: #49515d;
  font-size: 20px;
  font-weight: 500;
}
.title-color {
  color: #2eca7f;
}
.fa-bars {
  font-size: 30px;
}

.name {
  font-family: "Montserrat";
}
.link {
  color: #8e939a;
  font-size: 18px;
  margin: 0 10px;
  background: none;
  cursor: pointer;
  transition: 0.5s;
  position: relative;
  padding: 10px 20px;
  overflow: hidden;
  font-weight: 400;
  margin-top: 10px;
  text-align: center;
}
.link:hover {
  color: #424242;
}
.link::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: #2eca7f;
  z-index: -1;
  transition: 0.5s;
  bottom: 0;
  border-radius: 50% 50% 0 0;
}
.link:hover::before {
  height: 180%;
}
.link:focus {
  color: #424242;
}
.link:focus:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 50%;
  width: 50%;
  transform: translate(50%, -50%);
  border-bottom: 2px solid #2eca7f;
}

.about-area {
  padding: 30px 50px 50px;
}
.text-justify {
  text-align: justify;
}

.btn-2 {
  text-transform: uppercase;
  background-color: #ffffff;
  font-weight: 500;
  font-size: 14px;
  color: #212121;
  outline: none;
  box-shadow: 0 1px 1px rgb(0 0 0 / 10%), 0 2px 4px rgb(0 0 0 / 30%);
}
.info-list {
  display: inline-block;
  margin: 0;
  padding: 0;
  list-style: none;
}
.item {
  margin: 10px 0;
  padding: 5px;
  text-align: left;
}
.title {
  display: inline-block;
  min-width: 120px;
  font-size: 16px;
}
.value {
  display: inline-block;
  color: #9e9e9e;
  font-weight: 500;
}
.email {
  color: #2eca7f;
}

.page-header {
  border-radius: 15px;
  background-color: #ffff;
  box-shadow: 0 15px 25px 0 rgb(0 0 0 / 10%);
}
.header-info {
  background-color: #2eca7f;
  border-radius: 15px;
  background-image: url(/static/media/bg.e7ec0e11.png);
  background-size: cover;
  -webkit-background-size: cover;
  background-attachment: scroll;
  background-position: center center;
  background-repeat: no-repeat;
  margin-bottom: 25px;
  position: relative;
}
.image {
  margin-bottom: -75px;
}
.home-title {
  color: #fff;
  font-size: 54px;
  font-weight: 700;
  margin: 30px 0 0;
  font-family: "Montserrat";
}
.sub-title {
  color: #fff;
  font-size: 20px;
  margin: 10px 0;
  font-weight: bold;
}
.icon-link {
  color: #9e9e9e;
  margin: 5px;
}
.icon-link:hover {
  color: #9e9e9e;
}
.fab {
  height: 40px;
  width: 40px;
  background-color: white;
  font-size: 20px;
  line-height: 2;
  border-radius: 50%;
}
.padding {
  padding: 40px;
}
@media screen and (max-width: 576px) {
  .padding {
    padding: 10px;
  }
}

.heading {
  background-color: #2eca7f;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-image: url(/static/media/bg.e7ec0e11.png);
  background-size: cover;
  -webkit-background-size: cover;
  background-attachment: scroll;
  background-position: center center;
  background-repeat: no-repeat;
}
.heading h2 {
  color: #ffff;
  font-size: 50px;
  font-weight: 700;
  line-height: 1.5em;
  letter-spacing: 2;
  padding: 20px;
  margin: 0;
}
.title2 {
  color: #2eca7f;
}
.timeline {
  border-left: 2px solid #2eca7f;
  padding: 15px 0;
}
.timeline-item {
  position: relative;
  padding: 15px 20px 10px;
  margin-left: 20px;
  margin-bottom: 20px;
  background-color: #fff;
  border-left: 2px solid #2eca7f;
  box-shadow: 0 0 3px 0 rgb(0 0 0 / 20%);
}
.item-title {
  font-size: 20px;
  font-weight: 700;
}
.timeline-item:before {
  content: "";
  display: inline-block;
  position: absolute;
  background-color: #fff;
  border-radius: 10px;
  width: 12px;
  height: 12px;
  top: 20px;
  left: -29px;
  border: 2px solid #2eca7f;
  z-index: 2;
}
.timeline-item:after {
  content: "";
  display: inline-block;
  position: absolute;
  background-color: #2eca7f;
  border-radius: 10px;
  width: 29px;
  height: 2px;
  top: 25px;
  left: -29px;
  z-index: 1;
}
.timeline-item .item-period {
  display: inline-block;
  font-size: 14px;
  line-height: 1.2em;
  color: #2eca7f;
  margin-bottom: 8px;
  font-weight: 500;
}
.skills-info h4 {
  font-size: 18px;
  font-weight: 700;
  line-height: 1.1em;
  position: relative;
  margin: 0 0 5px;
}
.skill-container {
  background-color: #f5f5f5;
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  height: 12px;
  margin-bottom: 12px;
  width: 100%;
}
.skill-percentage {
  background-color: #2eca7f;
  height: 10px;
  padding: 0;
}
.skill-1 {
  width: 90%;
}
.skill-2 {
  width: 70%;
}
.skill-3 {
  width: 85%;
}
.skill-4 {
  width: 70%;
}
.skill-5 {
  width: 80%;
}
.skill-6 {
  width: 65%;
}
@media only screen and (max-width: 576px) {
  .heading h2 {
    padding: 5px;
    font-size: 30px;
  }
}

